export default {
	props:{
		resdata:Array,
		height:{
			type:String,
			default:"350"
		},
		//是否预览图片，默认false
		preview:{
			type:Boolean,
			default:false
		}
	},
	computed: {
		getStyle() {
			return `height:${this.height}rpx`
		},
		getUrls() {
			return this.resdata.map((v)=> v.src)
		}
	},
	methods:{
		event(item,index){
			console.log('点击了轮播图')
			if (this.preview) {
				//预览图片的效果
				return uni.previewImage({
					current:index,
					urls:this.getUrls,
					//样式指示器
					indicator:"default"
				})
			}
		}
	}
}
